.card {
  background-color: cadetblue;
  width: 300px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  box-shadow: 5px 5px 10px black;
  font-family: sans-serif;
  gap: 10px;
}
.table {
  display: flex;
  gap: 30px;
  justify-content: center;
  flex-wrap: wrap;
}

h2 {
  color: white;
  font-size: 32px;
}

.temperature {
  color: rgb(190, 49, 49);
}

.humidity {
  color: rgb(21, 21, 148);
}

.temperature,
.humidity {
  font-size: 30px;
}

h1,
h2,
h3,
p,
body {
  margin: 0;
  padding: 0;
}

h1 {
  font-family: sans-serif;
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: center;
  border-bottom: 3px solid black;
  margin-bottom: 30px;
  box-shadow: 0px 0px 20px black;
}
